    transition: all 0.2s cubic-bezier(0.3, 0.1, 0.6, 1); }
    .list-graph .top-bar .control-btn:hover, .list-graph .top-bar .control-btn.active {
      color: #000; }
    .list-graph .top-bar .control-btn:hover {
      background: #efefef; }
