    .list-graph .top-bar .sort-precision:hover [class*=' icon-'],
    .list-graph .top-bar .sort-recall:hover [class^='icon-'],
    .list-graph .top-bar .sort-recall:hover [class*=' icon-'],
    .list-graph .top-bar .sort-name:hover [class^='icon-'],
